import React, { Component, Fragment } from "react";
import { Link, match, Redirect } from "react-router-dom";
import {
  MdAccountCircle,
  MdClose,
  MdErrorOutline,
  MdLockOutline,
  MdNavigateNext,
} from "react-icons/md";
import Table from "../../components/Table/Table";
import { connect } from "react-redux";
import { StoreState } from "../../reducers";
import { Auth } from "../../actions";
import {
  SystemUserRoleInterface,
  UserInterface,
} from "../../shared/interfaces";
import { getRoleById } from "../../shared/dataTest";
import { blockUserToSystem, unBlockUserToSystem } from "../../actions/users";

// props for the component
interface AppProps {
  auth: Auth;
  userList: UserInterface[];
  accessRoles: SystemUserRoleInterface[];
  blockUserToSystem: Function;
  unBlockUserToSystem: Function;
  match: match<{
    user_id: string;
  }>;
}

// states for the component
interface AppState {
  loading: boolean;
  loadindRequest: boolean;
  userInfo: UserInterface | null;
  feedBack: {
    type: "success" | "danger" | "warning" | "";
    message: string;
  } | null;
  error: {
    target: string;
    msg: string;
  };
  redirect: boolean;
}

export class _UserDetails extends Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      loading: false,
      loadindRequest: false,
      userInfo: null,
      feedBack: null,
      error: {
        target: "",
        msg: "",
      },
      redirect: false,
    };
  }

  componentDidMount = () => {
    this.setState({
      loading: true,
    });
    const findUser = this.props.userList.filter(
      (user) => user.user_id === this.props.match.params.user_id
    );
    if (findUser.length <= 0) this.setState({ redirect: true });
    else this.setState({ userInfo: findUser[0], loading: false });
  };

  tableData = (): JSX.Element[] => {
    return [
      <tr key={1} className="border-b wrap border-gray-200 hover:bg-gray-100">
        <td className="p-3 text-left">Names</td>
        <td className="p-3 text-left font-bold">
          {this.state.userInfo?.user_info.fname +
            " " +
            this.state.userInfo?.user_info.lname}
        </td>
      </tr>,
      <tr key={2} className="border-b wrap border-gray-200 hover:bg-gray-100">
        <td className="p-3 text-left">NID</td>
        <td className="p-3 text-left font-bold">{this.state.userInfo?.n_id}</td>
      </tr>,
      <tr key={3} className="border-b wrap border-gray-200 hover:bg-gray-100">
        <td className="p-3 text-left">Gender</td>
        <td className="p-3 text-left font-bold">
          {this.state.userInfo?.user_info.sex === "M" ? "Male" : "Female"}
        </td>
      </tr>,
      <tr key={4} className="border-b wrap border-gray-200 hover:bg-gray-100">
        <td className="p-3 text-left">DOB</td>
        <td className="p-3 text-left font-bold">
          {this.state.userInfo?.user_info.date_of_birth}
        </td>
      </tr>,
      <tr key={5} className="border-b wrap border-gray-200 hover:bg-gray-100">
        <td className="p-3 text-left">Email</td>
        <td className="p-3 text-left font-bold">
          {this.state.userInfo?.email}
        </td>
      </tr>,
      <tr key={6} className="border-b wrap border-gray-200 hover:bg-gray-100">
        <td className="p-3 text-left">Phone</td>
        <td className="p-3 text-left font-bold">
          +{this.state.userInfo?.phone_number}
        </td>
      </tr>,
      <tr key={7} className="border-b wrap border-gray-200 hover:bg-gray-100">
        <td className="p-3 text-left">Address</td>
        <td className="p-3 text-left font-bold">
          {this.state.userInfo?.user_info.province} /{" "}
          {this.state.userInfo?.user_info.district} /{" "}
          {this.state.userInfo?.user_info.sector}
        </td>
      </tr>,
    ];
  };

  blockUser = (
    user_id: string,
    actionType: "block" | "un-block" = "block"
  ): void => {
    this.setState({ feedBack: { type: "", message: "" } });
    if (!user_id) {
      this.setState({
        feedBack: {
          type: "warning",
          message: `User Access is required doesn't exist`,
        },
      });
    }

    this.setState({ loadindRequest: true });

    if (!window.confirm(`Are sure you want to ${actionType} this user?`)) {
      return this.setState({ loadindRequest: false });
    }
    actionType === "block"
      ? this.props.blockUserToSystem(user_id)
      : this.props.unBlockUserToSystem(user_id);

    this.setState({
      feedBack: {
        type: "success",
        message: `User has been ${actionType}ed!`,
      },
      loadindRequest: false,
    });
    user_id = "";
  };

  render() {
    if (!this.state.loading && this.state.redirect === true) {
      return <Redirect to="/users" />;
    }
    if (this.state.loading === true || this.state.userInfo === null) {
      return (
        <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
          Loading User Details...
        </div>
      );
    }
    return (
      <Fragment>
        <div className="container mx-auto p-5">
          {this.state.feedBack && this.state.feedBack.type !== "" && (
            <div
              className={`alert-${this.state.feedBack.type} flex flex-row items-center relative mb-3`}
            >
              <span className="absolute top-0 bottom-0 right-0 px-4 py-3">
                <MdClose
                  className="h-5 w-5 text-gray-700 hover:font-bold"
                  title="Close"
                  onClick={() =>
                    this.setState({ feedBack: { type: "", message: "" } })
                  }
                />
              </span>
              <MdErrorOutline className="h-10 w-10" />
              <div className="alert-content ml-4">
                <div className="alert-title font-semibold text-lg">
                  {this.state.feedBack.type === "success" ? "Success" : "Error"}
                </div>
                <div className="alert-description text-sm">
                  {this.state.feedBack.message}
                </div>
              </div>
            </div>
          )}
          <div className="w-full">
            <div className="bg-blue-700 flex items-center w-full text-gray-100 gap-1 py-3 px-3 mb-3">
              <Link to="/users" className="uppercase">
                Users
              </Link>
              <MdNavigateNext className="w-7 h-5" />
              <span className="">{this.state.userInfo.user_info.lname}</span>
            </div>
            <div className="w-full flex items-center bg-white flex-col md:flex-row md:items-start">
              {/* left part */}
              <div className="w-1/4 flex flex-col text-center">
                <div className="image overflow-hidden">
                  {this.state.userInfo.user_info.photo &&
                  this.state.userInfo.user_info.photo !== "pic" ? (
                    <img
                      className="h-auto w-7/12 mx-auto rounded-full"
                      src={this.state.userInfo.user_info.photo}
                      alt=""
                    />
                  ) : (
                    <MdAccountCircle className="w-28 h-28 mx-auto" />
                  )}
                </div>

                <div className="flex justify-center my-2">
                  <h3 className="text-gray-800 text-xl text-bold uppercase">
                    {this.state.userInfo.default_access &&
                      getRoleById(
                        this.props.accessRoles,
                        this.state.userInfo.default_access.system_user_role_id
                      )?.role}
                  </h3>
                </div>

                <div className="my-1">
                  {this.state.userInfo.is_active === false ? (
                    <span className="bg-red-700 py-1 px-2 rounded text-white text-sm">
                      Blocked
                    </span>
                  ) : (
                    <span className="bg-green-700 py-1 px-2 rounded text-white text-sm">
                      Active
                    </span>
                  )}
                </div>

                <div className="flex flex-col items-center mt-3 gap-2">
                  <div>
                    <Link
                      className="btn text-green-700 border-green-700 md:border-2 hover:bg-green-700 hover:text-white transition ease-out duration-500 flex items-center"
                      title="View User Profile"
                      to={`/users/access/${this.props.match.params.user_id}`}
                    >
                      <MdLockOutline />
                      User Access
                    </Link>
                  </div>

                  <div>
                    <button
                      className="
                  btn
                  text-red-700
                  border-red-700
                  md:border-2
                  hover:bg-red-700
                  hover:text-white
                  transition
                  ease-out
                  duration-500
                  flex items-center
                "
                      title="Block User Account"
                      onClick={() =>
                        !this.state.loadindRequest &&
                        this.state.userInfo &&
                        this.blockUser(this.state.userInfo.user_id)
                      }
                    >
                      <MdLockOutline />
                      Disable user
                    </button>
                  </div>
                </div>
              </div>
              {/* end left part */}

              {/* right part */}
              <div className="w-3/4 flex flex-col p-3 border-dashed md:border-l md:border-t-0 border-t-2 md:border-gray-300">
                <div className="border-b border-gray-400 text-4xl font-semibold text-gray-700 py-2">
                  {this.state.userInfo.user_info.lname}'s details
                </div>

                <Table
                  columns={[
                    { columnTitle: "Names", alignText: "left", padding: "p-3" },
                    { columnTitle: "", alignText: "left", padding: "p-3" },
                  ]}
                  rows={this.tableData}
                  pagination={false}
                  hideColumns={true}
                  search={false}
                />
              </div>
              {/* end right part */}
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  users,
  access,
}: StoreState): {
  auth: Auth;
  userList: UserInterface[];
  accessRoles: SystemUserRoleInterface[];
} => {
  return { auth: auth, userList: users, accessRoles: access.accessRoles };
};

export const UserDetails = connect(mapStateToProps, {
  blockUserToSystem,
  unBlockUserToSystem,
})(_UserDetails);
