import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import {
  AssignRoleToUserActionInterface,
  CustomeAccessInterface,
  UserInterface,
} from "../shared/interfaces";
import { API } from "../utils/api";
import { setAxiosToken } from "../utils/AxiosToken";

/**
 * * ****************************** INTERFACES *****************************
 */

interface FetchUsersAction {
  type: ActionTypes.FETCH_USERS_ACCESS;
  payload: UserInterface[];
}

interface BlockUserToSystem {
  type: ActionTypes.BLOCK_USER_TO_SYSTEM | ActionTypes.UNBLOCK_USER_TO_SYSTEM;
  payload: string;
}

interface AssignRoleToUserAction {
  type: ActionTypes.ASSIGN_ROLE_TO_USER;
  payload: AssignRoleToUserActionInterface;
}

interface CustomizeUserAccessAction {
  type: ActionTypes.CUSTOMIZE_USER_ACCESS;
  payload: CustomeAccessInterface;
}

interface ResetUserAccessAction {
  type: ActionTypes.RESET_USER_ACCESS;
  payload: string;
}

export interface VerifyDocumentInterface {
  user_id: string;
  doc_id: string;
  validation_status: "waiting" | "approved" | "rejected";
  validated_by: {
    user_id: string;
    comment: string;
    validation_status: "waiting" | "approved" | "rejected";
    date?: string;
  };
}

interface VerifyAllDocumentAction {
  type: ActionTypes.VERIFY_ALL_DOCUMENT;
  payload: VerifyDocumentInterface;
}

export type AccessUserActionType =
  | FetchUsersAction
  | BlockUserToSystem
  | AssignRoleToUserAction
  | CustomizeUserAccessAction
  | ResetUserAccessAction
  | VerifyAllDocumentAction;

/**
 * * ****************************** ACTIONS *****************************
 */

export const fetchUserAccess = () => {
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<UserInterface[]>(`${API.AUTH}/users`);
      console.log({ users_response: res });
      dispatch<FetchUsersAction>({
        type: ActionTypes.FETCH_USERS_ACCESS,
        payload: res.data,
      });
    } catch (error) {
      console.error({ user_error: error });
    }
  };
};

export const blockUserToSystem = (data: string) => {
  console.log({ data });
  return async (dispatch: Dispatch) => {
    dispatch<BlockUserToSystem>({
      type: ActionTypes.BLOCK_USER_TO_SYSTEM,
      payload: data,
    });
  };
};

export const unBlockUserToSystem = (data: string) => {
  return async (dispatch: Dispatch) => {
    dispatch<BlockUserToSystem>({
      type: ActionTypes.UNBLOCK_USER_TO_SYSTEM,
      payload: data,
    });
  };
};

export const assignRoleToUserAccess = (
  data: AssignRoleToUserActionInterface
) => {
  console.log({ data });
  return async (dispatch: Dispatch) => {
    dispatch<AssignRoleToUserAction>({
      type: ActionTypes.ASSIGN_ROLE_TO_USER,
      payload: data,
      // payload: response.data,
    });
  };
};

export const customizeUserAccess = (data: CustomeAccessInterface) => {
  return async (dispatch: Dispatch) => {
    dispatch<CustomizeUserAccessAction>({
      type: ActionTypes.CUSTOMIZE_USER_ACCESS,
      payload: data,
    });
  };
};

export const resetUserAccess = (data: string) => {
  return async (dispatch: Dispatch) => {
    dispatch<ResetUserAccessAction>({
      type: ActionTypes.RESET_USER_ACCESS,
      payload: data,
    });
  };
};

export const verifyAllDocument = (data: VerifyDocumentInterface) => {
  console.log({ data });
  return async (dispatch: Dispatch) => {
    dispatch<VerifyAllDocumentAction>({
      type: ActionTypes.VERIFY_ALL_DOCUMENT,
      payload: data,
    });
  };
};
